/**
 * Card component.
 *
 * 1. We have no reliable way to be sure of the depth of applicability.
 */

.c-media-grid__img,
.c-media-grid__body {
	max-width: 30rem;
}

.c-media-grid.w-lte-43rem > .c-media-grid__img,
	.c-media-grid.w-lte-43rem > .c-media-grid__body {
		margin-left: auto;
		margin-right: auto;
	}

.c-media-grid.w-lte-43rem > .c-media-grid__img,
	.c-media-grid.w-lte-43rem > .c-media-grid__body {
		margin-top: 1rem;
	}

.c-media-grid.w-gt-43rem {
	display: grid;
	gap: 1rem;
	grid-template-columns: auto 33% 33% auto;
	grid-template-rows: auto;
}

.c-media-grid.w-gt-43rem > * {
		grid-row: var(--row);
	}

.c-media-grid.w-gt-43rem > .c-media-grid__img {
		grid-column: 2;
		justify-self: end;
	}

.c-media-grid.w-gt-43rem > .c-media-grid__img.is-odd {
		grid-column: 3;
		justify-self: start;
	}

.c-media-grid.w-gt-43rem > .c-media-grid__body {
		grid-column: 3;
		justify-self: start;
	}

.c-media-grid.w-gt-43rem > .c-media-grid__body.is-odd {
		grid-column: 2;
		justify-self: end;
	}

/**
 * @file
 * Header component (homepage) : mobile breakpoints.
 */

.c-header {
	position: relative;
	max-width: 78.5rem;
	margin-left: auto;
	margin-right: auto;
	background: var(--bg-primary);
	color: white;
}

.c-header__bg {
	display: none;
}

.c-header__logo {
	padding: 10%;
}

.c-header__logo-img {
		display: inline-block;
		max-width: 75%;
	}

@media (max-width:20.5em) {
		.c-header__logo-img {
			max-width: 85%;
		}
	}

.c-header__title {
	margin-bottom: var(--padding-percent);
	text-align: center;
}

.c-header__title-p1 {
	font-size: var(--font-size-ratio-l-rem);
	line-height: var(--line-height-l);
	}

.c-header__title-p1 {
		text-transform: uppercase;
		letter-spacing: var(--letter-spacing);
		font-weight: var(--font-weight-light);
	}

.c-header__title-p2 {
		text-transform: uppercase;
		letter-spacing: var(--letter-spacing);
		font-weight: var(--font-weight-bold);
	}

@media (min-width:26.5em) {
		.c-header__title-p1 {
	font-size: var(--font-size-ratio-ll-rem);
	line-height: var(--line-height-ll);
		}
		.c-header__title-p2 {
			@extend %fs-l;
		}
	}

.c-header__nav {
	text-align: center;
}

.c-header__menu.c-header__menu {
	justify-content: space-around;
}

.c-header__courses-title {
	background: white;
}

.c-header__courses-title-p1,
.c-header__courses-title-p2 {
	text-transform: uppercase;
	letter-spacing: var(--letter-spacing);
	text-align: center;
}

.c-header__courses-title-p1 {
	font-size: var(--font-size-ratio-xl-rem);
	line-height: var(--line-height-xl);
}

.c-header__courses-title-p1 {
	letter-spacing: var(--letter-spacing-m);
	color: #004281;
}

.c-header__courses-title-p2 {
	font-size: var(--font-size-ratio-l-rem);
	line-height: var(--line-height-l);
}

.c-header__courses-title-p2 {
	color: #d71920;
}

@media (min-width:26.5em) {
	.c-header__courses-title-p1 {
	font-size: var(--font-size-ratio-xll-rem);
	line-height: var(--line-height-xll);
	}
	.c-header__courses-title-p2 {
		@extend %fs-ll;
	}
}

/**
 * Header component (homepage) : tablet breakpoint.
 *
 * 1. Drupal system links on homepage >= tablet must accomodate multi-line
 * 	arrangement.
 */

@media (min-width:43em) {

	.c-header {
		background: white;
	}

	.c-header__bg,
	.c-header__nav,
	.c-header__title,
	.c-header__intro,
	.c-header__logo {
		position: absolute;
	}

	.c-header__bg {
		display: block;
		top: 0;
		left: 50%;
		margin-left: -60%;
		width: 110%;
	}
	.c-header__bg-img {
		width: 100%;
	}

	.c-header__logo.c-header__logo {
		top: 0;
		left: 0;
		padding: var(--padding-ratio-l-rem) 0 0 0;
		max-width: 37%;
		background: transparent;
	}


	.c-header__nav.c-header__nav {
		top: 0;
		/* left: 36%; */
		left: 32%;
		padding-top: 17%;
		z-index: 2;
		/* width: 64%; */
		width: 70%;
	}


	.c-header__menu.c-header__menu {
		display: block;
		padding-top: .5rem;
		width: 95%;
		text-align: right;
	}
	.c-header__menu.c-header__menu > .o-grid__item,
	.c-header__menu.c-header__menu > .o-grid__item.c-menu-main__item,
	.c-header__menu.c-header__menu > .o-grid__item.c-menu-main__item:hover {
		display: inline-block;
		vertical-align: middle;
		/*margin: 0 .33em .33em 0;*/
		margin-right: .33em;
	}
	.c-header__menu.c-header__menu > .c-menu-main__item > .c-menu-main__link {
		white-space: nowrap;
		display: inline-block;
		padding: .33em .5em;
	}


	.c-header__title {
		top: 0;
		left: 55%;
		width: 43%;
		text-align: left;
	}
	.c-header__title-p1 {
		font-size: var(--font-size-ratio-xl-rem);
		font-size: 5vw;
		line-height: 1.7;
		max-width: 9em;
		font-weight: var(--font-weight-thin);
	}
	.c-header__title-p2 {
		position: absolute;
		top: 0;
		left: .2em;
		padding-top: 17%;
	}
	.c-header__title-p2 {
	font-size: var(--font-size-ratio-rem);
	line-height: var(--line-height);
	}
	.c-header__title-p2 {
		font-size: 1.8vw;
	}


	.c-header__intro.c-header__intro {
		z-index: var(--z-index-std);
		top: 0;
		left: 48%;
		margin-left: -10rem;
		padding-top: 24.5%;
		width: 33rem;
		max-width: 48%;
	}

	.c-header__part2 {
		display: table;
		margin: 0 auto;
		padding-top: 58.5%;
		padding-bottom: var(--padding-percent);
		color: var(--text-color-inverse);
	}

	.c-header__courses-list,
	.c-header__courses-title {
		display: table-cell;
		vertical-align: top;
		padding-left: var(--padding-ratio-l-rem);
		color: inherit;
	}

	.c-header__courses-title {
		text-align: right;
	}
		.c-header__courses-title-p1,
		.c-header__courses-title-p2 {
			text-align: inherit;
			color: inherit;
			font-size: var(--font-size-ratio-xl-rem);
			line-height: var(--line-height-xl);
			letter-spacing: .05em;
		}
}

/**
 * @file
 * Header component (homepage) : desktop breakpoint.
 */

@media (min-width:75em) {

	.c-header {
		padding-bottom: 60%;
	}
	.c-header__inner-wrap {
		position: relative;
		left: 3%;
		width: 94%;
	}

	.c-header__title-p1 {
		font-size: var(--font-size-ratio-xxl-rem);
		line-height: 1.45;
		letter-spacing: var(--letter-spacing);
	}
	.c-header__title-p2 {
		padding-top: 5.15rem;
	}
	.c-header__title-p2 {
	font-size: var(--font-size-ratio-rem);
	line-height: var(--line-height);
	}

	.c-header__nav.c-header__nav {
		padding-top: 16.3%;
	}

	.c-header__intro.c-header__intro {
		padding-top: 20.5%;
		left: 56%;
		max-width: var(--w-typo-ratio-rem);
	}

	.c-header__part2 {
		position: absolute;
		top: 0;
		left: 0;
		margin-left: 3%;
		padding-top: 38%;
		color: white;
		width: 70%;
	}
	.c-header__courses-title {
		background: transparent;
	}
}

/**
 * @file
 * Header component (homepage) : wide + breakpoints.
 */

@media (min-width:81.25em) {

	.c-header {
		padding-bottom: 50rem;
	}
	.c-header__inner-wrap {
		position: static;
		left: 0;
		width: auto;
	}

	.c-header__menu.c-header__menu {
		display: flex;
		justify-content: space-between;
		flex-wrap: wrap;
		margin-left: auto;
		margin-right: auto;
		max-width: 45rem;
	}
	.c-header--home .c-header__menu.c-header__menu {
		margin-left: -2em;
		max-width: 90%;
	}
	.c-header__menu.c-header__menu > .o-grid__item.c-menu-main__item,
	.c-header__menu.c-header__menu > .o-grid__item.c-menu-main__item:hover {
		margin: 0;
	}

	.c-header__title {
		top: 1.25rem;
	}

	.c-header__nav.c-header__nav {
		left: 51%;
		padding-top: 16%;
		width: 49%;
	}

	.c-header__part2 {
		width: auto;
	}
}

/**
 * Messages component.
 */

:root {
	--c-messages-zindex: var(--z-index-overlay);
	--c-messages-bg-color: rgba(28, 28, 28, .88);
}

/**
 * Offcanvas component.
 *
 * Attempt to make it reusable : use 'rcomp' namespace.
 * @see https://github.com/reusable-components
 *
 * 1. Avoid initial glitch trade-off : require JS for slide out animation.
 * @see themes/chouette/vendor/Paulmicha/toggler.js
 */

:root {
	--rcomp-offcanvas-w: 42rem;
	--rcomp-offcanvas-z-index: var(--z-index-offcanvas);
	--rcomp-offcanvas-pad: 12.5%;
	--rcomp-offcanvas-close-btn-offset-t: calc(var(--rcomp-offcanvas-pad) / 3);
	--rcomp-offcanvas-close-btn-offset-r: calc(var(--rcomp-offcanvas-pad) / 2);
	--tr-rcomp-offcanvas-duration: .33s;
}

@keyframes tr-rcomp-offcanvas-slide-in {
	100% { transform: translateX(-100%); }
}

@keyframes tr-rcomp-offcanvas-slide-out {
	0% { transform: translateX(-100%); }
	100% { transform: translateX(0%); }
}

/**
 * Offcanvas component : bottom variant.
 *
 * Attempt to make it reusable : use 'rcomp' namespace.
 * @see https://github.com/reusable-components
 *
 * 1. Avoid initial glitch trade-off : require JS for slide out animation.
 * @see themes/chouette/vendor/Paulmicha/toggler.js
 */

:root {
	--rcomp-offcanvas-b-w: var(--w-typo-ratio-l-em);
	--rcomp-offcanvas-b-z-index: var(--z-index-offcanvas);
	--rcomp-offcanvas-b-pad: 5%;
	--tr-rcomp-offcanvas-b-duration: var(--tr-rcomp-offcanvas-duration);
	--rcomp-offcanvas-b-close-btn-offset-t: calc(var(--rcomp-offcanvas-pad) / 6);
	--rcomp-offcanvas-b-close-btn-offset-r: calc(var(--rcomp-offcanvas-pad) / 2);
}

@keyframes tr-rcomp-offcanvas-b-slide-in {
	100% { transform: translateY(-100vh); }
}

@keyframes tr-rcomp-offcanvas-b-slide-out {
	0% { transform: translateY(-100vh); }
	100% { transform: translateY(0vh); }
}

/**
 * Breadcrumb component.
 */

:root {
	--breadcrumb-bg-color: rgba(255, 255, 255, .9);
}

.c-breadcrumb__inner-wrap {
	display: inline-block;
	position: relative;
	z-index: 1;
	background: var(--breadcrumb-bg-color);
	box-shadow: 0 0 .4em .5em var(--breadcrumb-bg-color);
}

.c-breadcrumb__inner-wrap {
	font-size: var(--font-size-ratio-xs-rem);
	line-height: var(--line-height-xs);
}

.c-breadcrumb a,
	.c-breadcrumb span {
		display: inline-block;
	}

.c-breadcrumb__sep {
		vertical-align: -.05em;
	}

/**
 * Main menu component.
 *
 * 1. Homepage only : different background.
 * 2. Shame : @evol fix offset differently (figur out where it comes from).
 */

:root {
	--menu-main-item-bg-color: rgba(255, 255, 255, .1);
	--menu-main-item-bg-color-hover: white;
	--menu-main-tr-duration: .2s;
	--menu-main-item-link-color-hover: var(--text-color-inverse);
}

.c-menu-main {
	font-family: var(--font-familyB);
	font-weight: var(--font-weightB);
}

.fontB-is-loaded .c-menu-main {
	font-family: var(--font-familyB-loaded);
}

.c-menu-main__item {
	position: relative;
	text-align: center
}

.c-menu-main__item {
	font-size: var(--font-size-ratio-xs-rem);
	line-height: var(--line-height-xs)
}

@media (min-width:43em) {

  .c-menu-main__item {
	font-size: var(--font-size-ratio-s-rem);
	line-height: var(--line-height-s)
}
	}

@media (max-width:43em) {
	.c-menu-main__item {
	padding: 0 1%;
	}
}

@media (min-width:43em) {
	.c-header--home .c-menu-main__item { /* 1 */
	margin-bottom: .33em;
	background: var(--menu-main-item-bg-color);
	transition: background-color var(--menu-main-tr-duration) linear;
	}
	.c-header--home .c-menu-main__item:hover { /* 1 */
	margin-bottom: .33em;
	background: var(--menu-main-item-bg-color-hover);
	}
	.c-header--home .c-menu-main__item:hover > .c-menu-main__link, /* 1 */
	.c-header--home .c-menu-main__item > .c-menu-main__link:hover,
	.c-header--home .c-menu-main__item > .c-menu-main__link:focus,
	.c-header--home .c-menu-main__item > .c-menu-main__link:active { /* 1 */
	color: var(--menu-main-item-link-color-hover);
	}
}

.c-menu-main__deco {
	display: none;
	position: absolute;
	top: -3.25rem;
	left: 50%;
	margin-left: -7rem;
	width: 13rem;
}

@media (min-width:43em) {
	.c-menu-main__item.is-active > .c-menu-main__deco {
	display: block;
	}
}

.c-menu-main__deco-img {
	transform: scaleX(-1);
}

.c-menu-main__link {
	transition: color var(--menu-main-tr-duration) linear;
}


.c-menu-main__link {
	position: relative;
	z-index: 1;
}

.c-menu-main__link,
.c-menu-main__link:hover,
.c-menu-main__link:focus,
.c-menu-main__link:active {
	border: 0 none;
}

@media (max-width:43em) {
	.c-menu-main__link {
	display: inline-block;
	}
	.c-menu-main__link.active,
	.c-menu-main__item.is-active > .c-menu-main__link {
	border-bottom: 1px solid white;
	}
}

@media (min-width:43em) {
	.c-menu-main__link.active,
	.c-menu-main__item.is-active > .c-menu-main__link {
	border-top: 2px solid white;
	padding-top: .35rem;
	}
}

/**
 * Pagination component.
 *
 * Example :
 * <ul class="c-pagination ...">
 *  <li class="c-pagination__item ...">
 *  <a> ... </a>
 *  </li>
 * </ul>
 */

:root {
	--c-pagination--margin: var(--margin-ratio-xl-rem) auto 0 auto;
	--c-pagination--padding: var(--padding-ratio-s-rem) var(--padding-ratio-rem);
	--c-pagination--bg-color: rgba(0, 0, 0, .05);
	--c-pagination--bg-color-fh: rgba(255, 255, 255, .1);
}

/**
 * Subnav link component.
 */

.c-subnav-link {
	display: inline-block;
	border: 0 none;
	border-top: 3px solid transparent;
	border-bottom: 6px solid transparent;
	padding: var(--padding-ratio-s-rem) 0;
	color: #004281;
	font-family: var(--font-familyB);
	font-weight: var(--font-weightB);
	text-transform: uppercase;
	letter-spacing: var(--letter-spacing);
	transition : border 250ms ease-in-out;
	will-change: border;
}

.fontB-is-loaded .c-subnav-link {
	font-family: var(--font-familyB-loaded);
}

.c-subnav-link:hover {
	border-top: 3px solid #004281;
}

.c-subnav-link.active {
	border-bottom: 6px solid #004281;
}

/**
 * Paragraph with image component.
 */

:root {
	--c-pimg-margin-v: var(--line-height);
	--c-pimg-margin-v-em: calc(var(--c-pimg-margin-v) * 1em);
	--c-pimg-title-color: #004281;
}

/**
 * Text block component, kind of a text-box component variant.
 *
 * 1. Inner space so that full-width text doesn't appear too squished on tiny
 *  resolutions, but proportional to its container for when it gets bigger in
 *  order to avoid unpleasant proportions (too close to the edges).
 *  Update : used separate padding utilities.
 *  @see generic/box-model/_padding.css
 *
 * 2. Max length of line for a single-column page (the 66-character line).
 *  @see base/_typography.vars.css
 *
 * 3. As a block, it should be centered inside its container (or root element).
 */

.c-text-block {
	display: block;
	margin-right: auto; /* 3 */
	margin-left: auto; /* 3 */
	max-width: var(--w-typo-ratio-em); /* 2 */
}

.c-text-block--xl {
	display: block;
	margin-right: auto; /* 3 */
	margin-left: auto; /* 3 */
	max-width: 69em;
}

.c-text-block--xxl {
	display: block;
	margin-right: auto; /* 3 */
	margin-left: auto; /* 3 */
	max-width: 85em;
}

/**
 * Text box component.
 *
 * 1. Max length of line for a single-column page (the 66-character line).
 *  @see base/_typography.vars.css
 */

/**
 * Titles components.
 */

.c-title {
	font-family: var(--font-familyB);
	font-weight: var(--font-weightB);
}

.c-title {
	font-size: var(--font-size-ratio-xl-rem);
	line-height: var(--line-height-xl);
}

