 * 1. Attempt at economizing a superfluous BEM class.
 */

:root {
	--hgrid-padding-ratio: var(--padding-ratio);
	--hgrid-padding-ratio-rem: calc(var(--hgrid-padding-ratio) * 1rem);
	--hgrid-padding-ratio-s: var(--padding-ratio-s);
}

/**
 * Inline-block-based Grid object
 *
 * 1. Remove white space between children
 * 2. Portability
 * 3. Restore font-size from document,
 * 	@see base/_typography.vars.css
 * 4. Restore text-orientation inside grid item
 * 	@todo bi-directionnal text support
 *
 * @example
 * 	<div class="o-ibgrid o-ibgrid--gutter">
 * 	<div class="o-ibgrid__item">
 * 		...
 * 	</div>
 * 	<div class="o-ibgrid__item">
 * 		...
 * 	</div>
 * 	</div>
 *
 * @require base/_typography.vars.css
 * @require generic/_padding.css
 */

:root {
	--ibgrid-gutter: calc(var(--padding-ratio) / 2);
	--ibgrid-gutter-rem: calc(var(--ibgrid-gutter) * 1rem);
	--ibgrid-gutter-percent: calc(var(--padding-percent) / 2);
	--ibgrid-gutter-s: calc(var(--padding-ratio-s) / 2);
	--ibgrid-gutter-s-rem: calc(var(--ibgrid-gutter-s) * 1rem);
	--ibgrid-gutter-xs: calc(var(--padding-ratio-s) / 2.3);
	--ibgrid-gutter-xs-rem: calc(var(--ibgrid-gutter-xs) * 1rem);
	--ibgrid-gutter-l: calc(var(--padding-ratio-l) / 2);
	--ibgrid-gutter-l-rem: calc(var(--ibgrid-gutter-l) * 1rem);
	--ibgrid-line-height: var(--line-height);
}

.o-ibgrid {
	display: block;
	font-size: 0; /* 1 */
	padding: 0; /* 2 */
}

.o-ibgrid__item {
		display: inline-block;
		vertical-align: top;
		font-size: var(--font-size-ratio-rem); /* 3 */
	}

.o-ibgrid--gutter {
	margin-right: -var(--ibgrid-gutter-rem);
	margin-left: -var(--ibgrid-gutter-rem);
}

.o-ibgrid--gutter > .o-ibgrid__item {
		box-sizing: border-box; /* 2 */
		padding: 0 var(--ibgrid-gutter-rem);
	}

.o-ibgrid--gutter-l {
	margin-right: -var(--ibgrid-gutter-l-rem);
	margin-left: -var(--ibgrid-gutter-l-rem);
}

.o-ibgrid--gutter-l > .o-ibgrid__item {
		box-sizing: border-box; /* 2 */
		padding: 0 var(--ibgrid-gutter-l-rem);
	}

.o-ibgrid--gutter-percent {
	margin-right: -var(--ibgrid-gutter-percent);
	margin-left: -var(--ibgrid-gutter-percent);
}

.o-ibgrid--gutter-percent > .o-ibgrid__item {
		box-sizing: border-box; /* 2 */
		padding: 0 var(--ibgrid-gutter-percent);
	}

.o-ibgrid--center {
	text-align: center;
}

.o-ibgrid--center > .o-ibgrid__item {
		text-align: left; /* 4 */
	}

.o-ibgrid--inward > .o-ibgrid__item {
		text-align: right;
	}

.o-ibgrid--inward > .o-ibgrid__item + .o-ibgrid__item {
		text-align: left;
	}

.o-ibgrid--nowrap {
	white-space: nowrap;
}

.o-ibgrid--nowrap > .o-ibgrid__item {
		white-space: normal;
	}

.o-ibgrid--middle > .o-ibgrid__item {
		vertical-align: middle;
	}

/**
 * Table-based grid object.
 *
 * 1. inward variant : 2 cells text-aligned "towards" their side in contact.
 * 2. make the cell fill the remaining space.
 * 3. outward variant : 2 cells text-aligned in the opposite direction
 * of their side in contact.
 * 4. @todo : determine if it's more common to need with:100% by default or not.
 *
 * @see https://github.com/suitcss/components-arrange
 */

:root {
	--tgrid-padding-ratio: var(--padding-ratio);
	--tgrid-padding-ratio-rem: calc(var(--tgrid-padding-ratio) * 1rem);
	--tgrid-padding-ratio-s: var(--padding-ratio-s);
	--tgrid-padding-ratio-s-rem: calc(var(--tgrid-padding-ratio-s) * 1rem);
}

.o-tgrid {
	display: table;
	table-layout: auto;
	width: 100%;
}

.o-tgrid__item {
	display: table-cell;
	vertical-align: top;
	}

.o-tgrid--bottom > .o-tgrid__item {
	vertical-align: bottom;
	}

.o-tgrid--gutter > .o-tgrid__item {
	padding-right: var(--tgrid-padding-ratio-rem);
	}

.o-tgrid--gutter > .o-tgrid__item:last-child {
	padding-right: 0;
	}

/**
 * @stubbornella’s media object.
 *
 * Place any image- and text-like content side-by-side.
 * Adapted from InuitCSS's.
