	--w-typo-ratio: 33; /* 1 */
	--w-typo-ratio-em: calc(var(--w-typo-ratio) * 1em); /* 1 */
	--w-typo-ratio-rem: calc(var(--w-typo-ratio) * 1rem); /* 1 */
	--w-typo-ratio-l: 50;
	--w-typo-ratio-l-em: calc(var(--w-typo-ratio-l) * 1em);
