
/**
 * Generic styles :
 * Size utils
 */

.u-w-100,
.u-w-full {
  width: 100%;
}



/*  Min-widths.
*/

.u-mw-5 {
  min-width: 5em;
}

.u-minw-text-col {
  min-width: 10em;
}
.u-minw-text-col--l {
  min-width: 25em;
}

/*.u-minw-title-col {
  min-width: 30rem;
}*/

.u-minw-10 {
  min-width: 10%;
}

.u-w-xl {
  width: 62em;
}
.u-w-title-col {
  width: 18em;
}


/*  Max-widths.
*/

.u-maxw-full {
  max-width: 100%;
}

.u-mxw-1of3 {
  max-width: 33.333333%;
}
.u-mxw-2of3 {
  max-width: 66.666666%;
}

.u-maxw-typo {
  max-width: var(--w-typo-ratio-em);
}
@media (--mobile) {
  .u-maxw-typo--mobile {
	max-width: var(--w-typo-ratio-em);
  }
}
.u-maxw-typo-s {
  max-width: var(--w-typo-ratio-s-em);
}

.u-w-logo-pic {
  width: 6.7em;
}
@media (--mobile) {
  .u-w-logo-pic {
	width: 5em;
  }
}

.u-mxw-logo-pic {
  max-width: 6.7em;
}
@media (--mobile) {
  .u-mxw-logo-pic {
	max-width: 5em;
  }
}

.u-w-flag {
  width: 1.5rem;
}


.u-maxw-cta-burst {
  max-width: 10em;
  @media (--mobile) {
	max-width: 8em;
  }
}


.u-mxw-user-pic--s {
  max-width: 3.125rem;
}


/*  Proportional widths.
*/

.u-w-1of2 {
  width: 50%;
}
.u-w-1of3 {
  width: 33.333333%;
}
.u-w-2of3 {
  width: 66.666666%;
}
.u-w-1of4 {
  width: 25%;
}
.u-w-3of4 {
  width: 75%;
}
.u-w-1of6 {
  width: 16.666666%;
}
.u-w-5of6 {
  width: 83.333333%;
}

/*  Responsive proportional widths overrides.
*/

@media (--tablet) {
  .u-tablet-w-1of2 {
	width: 50%;
  }
  .u-tablet-w-1of3 {
	width: 33.333333%;
  }
  .u-tablet-w-2of3 {
	width: 66.666666%;
  }
  .u-tablet-w-1of4 {
	width: 25%;
  }
  .u-tablet-w-3of4 {
	width: 75%;
  }
  .u-tablet-w-1of6 {
	width: 16.666666%;
  }
  .u-tablet-w-5of6 {
	width: 83.333333%;
  }
}

@media (--desktop) {
  .u-desktop-w-1of2 {
	width: 50%;
  }
  .u-desktop-w-1of3 {
	width: 33.333333%;
  }
  .u-desktop-w-2of3 {
	width: 66.666666%;
  }
  .u-desktop-w-1of4 {
	width: 25%;
  }
  .u-desktop-w-3of4 {
	width: 75%;
  }
  .u-desktop-w-1of6 {
	width: 16.666666%;
  }
  .u-desktop-w-5of6 {
	width: 83.333333%;
  }
}

@media (--wide) {
  .u-wide-w-1of2 {
	width: 50%;
  }
  .u-wide-w-1of3 {
	width: 33.333333%;
  }
  .u-wide-w-2of3 {
	width: 66.666666%;
  }
  .u-wide-w-1of4 {
	width: 25%;
  }
  .u-wide-w-3of4 {
	width: 75%;
  }
  .u-wide-w-1of6 {
	width: 16.666666%;
  }
  .u-wide-w-5of6 {
	width: 83.333333%;
  }
}



.u-h-100,
.u-h-full {
  height: 100%;
}

