

/**
 * Layout utilities.
 */

.u-block {
	display: block;
}
.u-block--desc * {
	display: block;
}
.u-inline-block {
	display: inline-block;
}

.u-relative {
	position: relative;
}
.u-absolute {
	position: absolute;
}

.u-float-l {
	float: left;
}
.u-float-r {
	float: right;
}
.u-float-none {
	float: none;
}

.u-clear {
	clear: both;
}
.u-clear-l {
	clear: left;
}
.u-clear-r {
	clear: right;
}

.u-valign-t {
	vertical-align: top !important;
}
.u-valign-m {
	vertical-align: middle !important;
}
.u-valign-b {
	vertical-align: bottom !important;
}

.u-overflow-hidden {
	overflow: hidden;
}


/*  Squared container
*/

.o-squared {
	padding-bottom: 100%;
	position: relative;
}
.o-squared__content {
	position: absolute;
	top: 0;
	right: 0;
	bottom: 0;
	left: 0;
}


/*  Clearfix
*/

%cf {
	display: block;
	&::before,
	&::after {
		content: "";
		display: table;
	}
	&::after {
		clear: both;
	}
}

.cf {
	@extend %cf;
}
