
/**
 * Header component (homepage) : tablet breakpoint.
 *
 * 1. Drupal system links on homepage >= tablet must accomodate multi-line
 * 	arrangement.
 */

@media (--tablet) {

	.c-header {
		background: white;
	}

	.c-header__bg,
	.c-header__nav,
	.c-header__title,
	.c-header__intro,
	.c-header__tools,
	.c-header__logo {
		position: absolute;
	}

	.c-header__bg {
		display: block;
		top: 0;
		left: 50%;
		margin-left: -60%;
		width: 110%;
	}
	.c-header__bg-img {
		width: 100%;
	}

	.c-header__logo.c-header__logo {
		top: 0;
		left: 0;
		padding: var(--padding-ratio-l-rem) 0 0 0;
		max-width: 37%;
		background: transparent;
	}


	.c-header__tools {
		top: 0;
		left: 2%;
		z-index: 2;
		padding-top: 19%;
		max-width: 27%;
	}
	.c-header__tools a { /* 1 */
		margin-bottom: .33em;
	}


	.c-header__nav.c-header__nav {
		top: 0;
		/* left: 36%; */
		left: 32%;
		padding-top: 17%;
		z-index: 2;
		/* width: 64%; */
		width: 70%;
	}


	.c-header__menu.c-header__menu {
		display: block;
		padding-top: .5rem;
		width: 95%;
		text-align: right;
	}
	.c-header__menu.c-header__menu > .o-grid__item,
	.c-header__menu.c-header__menu > .o-grid__item.c-menu-main__item,
	.c-header__menu.c-header__menu > .o-grid__item.c-menu-main__item:hover {
		display: inline-block;
		vertical-align: middle;
		/*margin: 0 .33em .33em 0;*/
		margin-right: .33em;
	}
	.c-header__menu.c-header__menu > .c-menu-main__item > .c-menu-main__link {
		white-space: nowrap;
		display: inline-block;
		padding: .33em .5em;
	}


	.c-header__title {
		top: 0;
		left: 55%;
		width: 43%;
		text-align: left;
	}
	.c-header__title-p1 {
		font-size: var(--font-size-ratio-xl-rem);
		font-size: 5vw;
		line-height: 1.7;
		max-width: 9em;
		font-weight: var(--font-weight-thin);
	}
	.c-header__title-p2 {
		position: absolute;
		top: 0;
		left: .2em;
		padding-top: 17%;
		@extend %fs-reset;
		font-size: 1.8vw;
	}


	.c-header__intro.c-header__intro {
		z-index: var(--z-index-std);
		top: 0;
		left: 48%;
		margin-left: -10rem;
		padding-top: 24.5%;
		width: 33rem;
		max-width: 48%;
	}

	.c-header__part2 {
		display: table;
		margin: 0 auto;
		padding-top: 58.5%;
		padding-bottom: var(--padding-percent);
		color: var(--text-color-inverse);
	}

	.c-header__courses-list,
	.c-header__courses-title {
		display: table-cell;
		vertical-align: top;
		padding-left: var(--padding-ratio-l-rem);
		color: inherit;
	}

	.c-header__courses-title {
		text-align: right;
	}
		.c-header__courses-title-p1,
		.c-header__courses-title-p2 {
			text-align: inherit;
			color: inherit;
			font-size: var(--font-size-ratio-xl-rem);
			line-height: var(--line-height-xl);
			letter-spacing: .05em;
		}
}
